import 'focus-visible';
import React from 'react';
import { BaseButton, DEFAULT_PROPS as DEFAULT_PROPS_BASE_BUTTON } from 'shared/BaseButton';
import { ButtonCoreSize, ButtonCoreType, ShareableButtonProps } from 'shared/types/button';

import styles from 'shared/BaseButton/coreStyles.scss';

export const DEFAULT_PROPS = {
  type: 'primary',
  size: 'default'
};

export interface IconButtonProps extends Omit<ShareableButtonProps, 'iconLeft' | 'iconRight' | 'isIcon'> {
  /** Размер кнопки */
  size?: ButtonCoreSize;

  /** Тип кнопки */
  type?: ButtonCoreType;

  /** Контент в виде иконки */
  icon: React.ReactElement;

  /** Пропс для растягивания кнопки на ширину родительского контейнера */
  stretch?: boolean;
}

export const IconButton: React.FC<IconButtonProps> = ({
  icon,
  ...props
}) => (
  <BaseButton
    { ...props }
    isIcon
    styles={ styles }
    content={ icon }
  />
);

IconButton.defaultProps = {
  ...DEFAULT_PROPS,
  ...DEFAULT_PROPS_BASE_BUTTON
} as IconButtonProps;
